import { Union } from "./fable_modules/fable-library.3.6.2/Types.js";
import { union_type } from "./fable_modules/fable-library.3.6.2/Reflection.js";
import { map } from "./fable_modules/fable-library.3.6.2/Option.js";

export function getMessage(e) {
    let copyOfStruct = e;
    return copyOfStruct.GetMessage();
}

export class CreateUserError extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["InvalidEmail", "InvalidName", "InvalidPassword", "InvalidPasswordConfirmation", "EmailAlreadyExists"];
    }
    GetMessage() {
        const this$ = this;
        switch (this$.tag) {
            case 1: {
                return "Nome inválido";
            }
            case 2: {
                return "A senha deve conter 8 caracteres, incluindo uma letra maiúscula, uma letra minúscula, um número e um caracter especial";
            }
            case 3: {
                return "As senhas devem coincidir";
            }
            case 4: {
                return "Este e-mail já está cadastrado";
            }
            default: {
                return "E-mail inválido";
            }
        }
    }
}

export function CreateUserError$reflection() {
    return union_type("App.Errors.CreateUserError", [], CreateUserError, () => [[], [], [], [], []]);
}

